mt-logo {
  min-width: 200px;
  white-space: nowrap;
}
@media (max-width: $xs-width) {
  mt-logo {
    min-width: 10px;
  }
}

.logo {
  font-size: 1.9em;
}
.logo__base-text {
  font-family: $font__family--logo-base;;
}

.logo__accent-text {
  font-family: $font__family--logo-accent;
  color: $logo__accent-red;
}